.card{
	display: flex;
	justify-content: center;
}
.card .contenedorCard{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 250px;
	border-radius: 20px;
	padding: 0 5px;
	box-shadow: 1px 1px 1px 1px #00000033;
	margin: 0 0 10px 0;
	color: white;
}

.card img{
	width: 50%;
	padding: 5px;
	box-shadow: 1px 1px 1px 1px #00000033;
	border-radius: 50%;

}